// React
import {
  Modal, ModalOverlay, ModalContent,
  ModalBody, useDisclosure, Box
} from "@chakra-ui/react";
import { useState } from 'react';
// Assets
import ExampleBackground from './assets/images/example.png';
// Components
import AddToBasketBtn from './components/addToBasketBtn';
import CloseButton from './components/closeButton';
import ProductHeader from './components/productHeader';
import ProductImage from './components/productImage';
import ProductValue from './components/productValue';
import ProductOptions from './components/productOptions/productOptions';
// Library
import fetchProduct from './library/fetchProduct';

const ProductData = {
  id: 0,
  detail: {
    name: "...",
    description: "...",
    brand: {
      name: "..."
    },
    flags: { hasColors: false },
    numberOfColors: 0
  },
  imagery: {
    images: [
      {
        filePath: "6/optimized/11659936_fpx.tif"
      }
    ]
  },
  urlTemplate: {
    swatch: "https://images.bloomingdalesassets.com/is/image/BLM/swatches/",
    swatchSprite: "https://images.bloomingdalesassets.com/is/image/BLM/",
    product: "https://images.bloomingdalesassets.com/is/image/BLM/products/"
  },
  relationships: {
    taxonomy: {},
    upcs: {}
  }
}


function ProductModal(props:any) {
  const { isOpen, onOpen, onClose } = useDisclosure();
  const [ product, setProduct ] = useState(ProductData);
  const [ relations, setRelations ] = useState<any>({});

  // Only make API call if the loaded product is not already this one
  // if the user views this modal, click away and then comes straight back this one -> it won't need to load twice
  if (product.id !== props.id) {
    fetchProduct(
      props.id,
      null,
      (resp:any) => {
        setProduct(resp);
        setRelations(resp.relationships.upcs);
      }
    );
  } else {
    console.log(product);
    console.log(relations);
  }

  // Render the constructed modal for this specific product
  return <div className="main">
    <Modal onClose={onClose} isOpen={true} isCentered size="6xl">
      <ModalOverlay backgroundColor="transparent" filter="blur(20px)" backgroundImage={`url('${ExampleBackground}')`}/>
      <ModalContent h="700px">
        <CloseButton/>
        <ModalBody p="0px" h="100%" w="100%" borderRadius="5px" overflowY="hidden" overflowX="hidden">
          <Box display="flex" h="inherit" w="inherit">
            <ProductImage url={product.urlTemplate.product} imageList={product.imagery.images}/>
            <Box
              display="flex"
              flexDirection="column"
              justifyContent="space-between"
              overflowY="auto"
              w="50%"
              maxHeight="calc(100% - 130px)"
              padding="64px 64px 0 64px"
            >
              <ProductHeader
                name={product.detail.name}
                brand={product.detail.brand.name}
                description={product.detail.description}
              />
              <ProductOptions/>
              <ProductValue value="$190.38"/>
            </Box>
          </Box>
          <AddToBasketBtn/>
        </ModalBody>
      </ModalContent>
    </Modal>
  </div >
}


export default ProductModal;
