// React
import { useState } from 'react';
import { Text, Box } from "@chakra-ui/react";


const ProductHeader = (props:any) => {
  const [isReadMore, setReadMore] = useState(false);
  return <Box>
    <Text
      textTransform="uppercase"
      fontFamily="Avenir"
      fontWeight="600"
      fontSize="24px"
      lineHeight="32.78px"
      letterSpacing="4%"
    >{ props.brand }</Text>
    <Text
      fontFamily="Avenir"
      fontSize="22px"
      fontWeight="500"
      letterSpacing="1px"
      paddingBottom="16px"
    >{ props.name }</Text>
    <Text
      fontFamily="Avenir"
      fontSize="14px"
      fontWeight="400"
      letterSpacing="1px"
      lineHeight="18px"
      noOfLines={ isReadMore ? undefined : 3 }
    >{ props.description }</Text>
    <Text
      cursor="pointer"
      userSelect="none"
      fontFamily="Avenir"
      fontSize="14px"
      fontWeight="400"
      letterSpacing="1px"
      textDecoration="underline"
      padding="8px 0 0 0"
      width="fit-content"
      height="fit-content"
      onClick={ () => setReadMore(!isReadMore) }
    >{ isReadMore ? "Show Less" : "Read More" }</Text>
  </Box>
}


export default ProductHeader;
